import React from 'react';
import './Application.css'

type PropsType = {
    application: Object,
};

const images = {
    "AppleTv": require("../Resources/Images/AppleTv.png"),
    "Aurmatic": require("../Resources/Images/Aurmatic.png"),
    "DarkSky": require("../Resources/Images/DarkSky.png"),
    "Facetune": require("../Resources/Images/Facetune.png"),
    "FitOn": require("../Resources/Images/FitOn.png"),
    "iContacts": require("../Resources/Images/iContacts.png"),
    "Netflix": require("../Resources/Images/Netflix.png"),
    "Pommie": require("../Resources/Images/Pommie.png"),
    "Priori": require("../Resources/Images/Priori.png"),
    "Scrivener": require("../Resources/Images/Scrivener.png"),
    "Sibapp": require("../Resources/Images/Sibapp.jpg"),
    "Xmart": require("../Resources/Images/Xmart.png"),
};

export default class Application extends React.Component<PropsType> {
    render() {
        const {application} = this.props;
        return (
            <div className="application">
                <div className="imageContainer">
                    <img className="image" src={images[application.icon]} alt={application.title}/>
                </div>
                <div className="metadata">
                    <div className="title">
                        {application.title}
                    </div>
                    <div className="subtitle">
                        {application.subtitle}
                    </div>
                    <div className="bottomContainer">
                        <div className="price">
                            Price: {application.price}
                        </div>
                        <a className="link" href={application.appstore}>App-store link</a>
                    </div>
                </div>
                <a className="download" href={application.download}>
                    Download
                </a>
            </div>
        );
    }
}