import React from 'react';
import Application from "./Components/Application";
import data from './Resources/Data/app.json'
import _ from "lodash"

const minimumWidth = 350;

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = { width: 0, height: 0 };
        this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    }

    componentDidMount() {
        this.updateWindowDimensions();
        window.addEventListener('resize', this.updateWindowDimensions);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowDimensions);
    }

    updateWindowDimensions() {
        this.setState({ width: window.innerWidth, height: window.innerHeight });
    }

    renderApplications = (applications: Array)=> {
        const {width} = this.state;
        if (width === 0)
            return null;
        let itemsInOneRow = Math.floor(width / minimumWidth);
        let chunkedApplications = _.chunk(applications, itemsInOneRow);
        let outputArray = [];
        for (let i = 0; i < chunkedApplications.length; i++){
            outputArray[i] = (
                <div className="applicationRow" key={i}>
                    {chunkedApplications[i].map(app => (
                        <Application application={app} key={app.title}/>
                    ))}
                </div>
            )
        }
        return (
            outputArray.map(item => item)
        )
    };

    render() {
        return (
            <div>
                <header>
                    <h1>
                        Free iOS apps for everyone
                    </h1>
                    <p className="description">
                        Visit this site with iOS mobile to be able download free apps.
                        <br/>
                        We've planned to add lots of free and awesome apps to below list.
                    </p>
                    <p>
                        Be tuned and wait for new apps!
                    </p>
                </header>
                <div>
                    {this.renderApplications(data)}
                </div>
            </div>
        );
    }
}
export default App;
